<template>
  <div class="container">
	<div class="faceid">
		<div class="top-tip">请将人像保持在取景框中</div>
		<!--图片展示-->
		<div class="video-hole">
			<canvas ref="canvas" class="canvas"></canvas>
			<video ref="video" class="video" autoplay></video>
		</div>
	</div>
	<!-- 
	<div class="button-container">
		<button @click="callCamera" class="open">打开摄像头</button>
		<button @click="closeCamera" class="close">关闭摄像头</button>
	</div>
	 -->
  </div>
</template>

<script>
export default {
  data () {
    return {}
  },
  created: function() {
	  this.callCamera();
  },
  methods: {
    // 调用摄像头
    callCamera () {
      // H5调用摄像头API
      navigator.mediaDevices.getUserMedia({
        video: true
      }).then(success => {
        // 摄像头开启成功
        this.$refs['video'].srcObject = success
        // 实时拍照效果
        this.$refs['video'].play()
      }).catch(error => {
        console.error(error,'摄像头开启失败，请检查摄像头是否可用！')
      })
    },
    // 拍照
    // photograph () {
    //   let ctx = this.$refs['canvas'].getContext('2d')
    //   // 把当前视频帧内容渲染到canvas上
    //   ctx.drawImage(this.$refs['video'], 0, 0, 640, 480)
    //   // 转base64格式、图片格式转换、图片质量压缩
    //   let imgBase64 = this.$refs['canvas'].toDataURL('image/jpeg', 0.7)

    //   // 由字节转换为KB 判断大小
    //   let str = imgBase64.replace('data:image/jpeg;base64,', '')
    //   let strLength = str.length
    //   let fileLength = parseInt(strLength - (strLength / 8) * 2)
    //   // 图片尺寸  用于判断
    //   let size = (fileLength / 1024).toFixed(2)
    //   console.log(size)
      // 上传拍照信息  调用接口上传图片 .........

      // // 保存到本地
      // let ADOM = document.createElement('a')
      // ADOM.href = this.headImgSrc
      // ADOM.download = new Date().getTime() + '.jpeg'
      // ADOM.click()
    // },
    // 关闭摄像头
    closeCamera () {
      if (!this.$refs['video'].srcObject) return
      let stream = this.$refs['video'].srcObject
      let tracks = stream.getTracks()
      tracks.forEach(track => {
        track.stop()
      })
      this.$refs['video'].srcObject = null
    },
  }
}
</script>

<style scoped>
	.open{
		width: 100px; height: 50px;
	}
	.close{
		width: 100px; height: 50px;
	}
	.button-container{
		width: 300px;
		display: flex;
		justify-content: space-around;
	}
	.faceid{
		width: 100%;
		height: 500px;
		top: 50px;
		/* background-color: red; */
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.top-tip{
		font-size: 24px;
		margin-top: 50px;
	}
	.canvas{
		width: 200px; height: 200px; display: none;
	}
	.video{
		width: 280px; 
		height: 280px;
		position: absolute;
		margin: -40px 0 0 -40px;
	}
	.video-hole{
		width: 200px;
		height: 200px;
		border: 2px solid white;
		border-radius: 50%;
		background: none;
		margin-top: 50px;
		position: relative;
		overflow: hidden;
	}
</style>
